import Vue from "vue";
import Vuex from "vuex";
import GetSheetDone from "get-sheet-done";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // The state that we want to track in this application
    unansweredQuestions: [], // Pool of questions to be shown to the user
    answeredQuestions: [], // Questions that have been correctly answered
    currentQuestion: {
      question: "Sample question",
      answers: [],
    }, // Will be overwritten immediately
    cardFlipped: false, // Whether to show the question or answer
  },
  getters: {
    currentQuestion(state) {
      if (!state.currentQuestion) {
        return null;
      }
      return state.currentQuestion.question;
    },
    currentAnswers(state) {
      if (!state.currentQuestion) {
        return null;
      }
      return state.currentQuestion.answers;
    },
  },
  mutations: {
    // The changes to the state that we'll be making
    setUnanswered(state, unansweredQuestions) {
      state.unansweredQuestions = unansweredQuestions;
    },
    pushUnanswered(state, question) {
      // When a question was answered incorrectly
      state.unansweredQuestions = state.unansweredQuestions.filter(function(q) {
        return q !== question;
      });

      state.unansweredQuestions.push(question);
    },
    pushAnswered(state, question) {
      // When a question was answered correctly
      state.unansweredQuestions = state.unansweredQuestions.filter(function(q) {
        return q !== question;
      });

      state.answeredQuestions.push(question);
    },
    setCurrentQuestion(state, question) {
      // Setting the question to be rendered
      question.answers = shuffle(question.answers);
      state.currentQuestion = question;
      state.cardFlipped = false;
    },
    flipCard(state) {
      state.cardFlipped = !state.cardFlipped;
    },
  },
  actions: {
    setUnanswered(context) {
      // To intitially load the questions from the JSON file you made earlier

      let DOC_KEY = "11l5JAIhD7FskgEChYJ6zV7-fZMymaXpfOuJzOWWqSHk";

      let questionsFromSheet = [];

      GetSheetDone.labeledCols(DOC_KEY).then(function(sheet) {
        let answers;
        sheet.data.forEach(function(questionRow) {
          answers = [];
          if (questionRow.answeratext) {
            answers.push({
              text: questionRow.answeratext,
              correct: questionRow.answeracorrect === "TRUE",
            });
          }
          if (questionRow.answerbtext) {
            answers.push({
              text: questionRow.answerbtext,
              correct: questionRow.answerbcorrect === "TRUE",
            });
          }
          if (questionRow.answerctext) {
            answers.push({
              text: questionRow.answerctext,
              correct: questionRow.answerccorrect === "TRUE",
            });
          }
          if (questionRow.answerdtext) {
            answers.push({
              text: questionRow.answerdtext,
              correct: questionRow.answerdcorrect === "TRUE",
            });
          }

          questionsFromSheet.push({
            question: questionRow.question,
            answers,
          });
        });

        context.commit("setUnanswered", questionsFromSheet);
        context.commit("setCurrentQuestion", firstQuestion(context));
      });
    },
    init(context) {
      context.commit("setCurrentQuestion", firstQuestion(context));
    },
    correctAnswer(context) {
      const question = context.state.currentQuestion;
      context.commit("pushAnswered", question);
      context.commit("flipCard");
    },
    wrongAnswer(context) {
      const question = context.state.currentQuestion;
      context.commit("pushUnanswered", question);
      context.commit("flipCard");
    },
  },
});

function firstQuestion(context) {
  const numQuestions = context.state.unansweredQuestions.length;

  if (numQuestions > 0) {
    return context.state.unansweredQuestions[0];
  } else {
    return null;
  }
}

function shuffle(array) {
  var currentIndex = array.length,
    temporaryValue,
    randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {
    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  return array;
}
