import Vue from "vue";
import Vuex from "vuex";
import Notifications from "vue-notification";

import App from "./App.vue";
import store from "./store";

Vue.config.productionTip = false;
Vue.use(Vuex);
Vue.use(Notifications);

new Vue({
  render: h => h(App),
  store,
  mounted() {
    // Added functionality
    this.$store.dispatch("setUnanswered");
  },
}).$mount("#app");
